'use strict';

(function (factory) {

    "use strict";

    if (typeof define === 'function' && define['amd']) {
        define(['knockout', 'text!./view.html'], factory);
    }
})(function (ko, template) {

    function ViewModel(params) {

        this.route = ko.observable(params.route || '');

        this.route.subscribe(function (val) {

            if (val && val !== 'Navigate:') {
                window.location.assign(val);
            }
        });
    }

    return {
        viewModel: ViewModel,
        template: template
    };
});